<template lang="html">
  <div class="convert-list">
    <div class="convert-list-item clearfix borderB">
      <div class="convert-list-item__left fl" :style="{width: imgWidth}">
        <img :src="icon" alt="">
      </div>
      <div class="convert-list-item__center fl">
        <p class="center-text" v-if="isAd">{{text}}</p>
        <slot></slot>
        <p class="center-warn" v-show="!isReach">{{warn}}</p>
      </div>
      <div class="convert-list-item__right fr">
        <!-- <span disabled :class="[isReach ? 'activebgColor' : 'disablebgColor']" @click="$router.push('/convertresult')">立即兑换</span> -->
        <input type="button" value="立即兑换" :disabled="!isReach" :class="[isReach ? 'activebgColor' : 'disablebgColor']" @click="$router.push('/convertresult')" />
      </div>
    </div>

  </div>
</template>

<script>

export default {
  props: {
    isReach: {
      type: Boolean,
      default: false
    },
    isAd: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String,
      required: true
    },
    imgWidth: {
      type: String,
      default: '.8rem'
    },
    text: {
      type: String,
      default: 'VIP1需要消耗10000召币'
    },
    warn: {
      type: String,
      default: '您的VIP1级别未满，暂不能兑换VIP2'
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.convert-list {

  .convert-list-item {
    width: 100%;
    // height: 1.6rem;
    padding: 0 .22rem;
    font-size: .32rem;
    background: @whiteColor;
    .convert-list-item__left {
      height: 100%;
      img {
        width: .6rem;
        height: .6rem;
        margin-top: .5rem;
      }
    }
    .convert-list-item__center {
      width: 4.44rem;
      .center-text {
        margin-top: .64rem;
      }
      .center-warn {
        margin-top: .16rem;
        font-size: .24rem;
        color: @lightColor;
      }
    }
    .convert-list-item__right {
      input {
        display: block;
        width: 1.8rem;
        height: .88rem;
        line-height: .88rem;
        text-align: center;
        border-radius: .05rem;
        margin-top: .36rem;
        margin-bottom: .34rem;
        border: none;
        // background: @lightColor;
        color: @whiteColor;

        font-size: .34rem;
      }
    }
  }
}

</style>



