      <p class="center-slot">10000召币可兑换<span>5%</span>的广告费返点</p>
    </convert-list>
    <convert-list :isReach="true" :icon="icons[1]" imgWidth=".7rem">
      <p class="center-slot">20000召币可兑换<span>10%</span>的广告费返点</p>
    </convert-list>
    <convert-list :isReach="true" :icon="icons[2]" imgWidth=".7rem">
      <p class="center-slot">30000召币可兑换<span>15%</span>的广告费返点</p>
    </convert-list>
    <convert-list :isReach="true" :icon="icons[3]" imgWidth=".7rem">
      <p class="center-slot">40000召币可兑换<span>20%</span>的广告费返点</p>
    </convert-list>
    <convert-list :isReach="true" :icon="icons[4]" imgWidth=".7rem">
      <p class="center-slot">50000召币可兑换<span>25%</span>的广告费返点</p>
    </convert-list>
    <convert-list :isReach="true" :icon="icons[5]" imgWidth=".7rem">
