<script>
import defaultHead from '@/components/head/defaultHead'
import countItem from '@/components/personcenter/countItem'
import convertList from '@/components/convertVip/convertList'

import ad01 from '@/assets/img/convertAdRebate/ad01@2x.png'
import ad02 from '@/assets/img/convertAdRebate/ad02@2x.png'
import ad03 from '@/assets/img/convertAdRebate/ad03@2x.png'
import ad04 from '@/assets/img/convertAdRebate/ad04@2x.png'
import ad05 from '@/assets/img/convertAdRebate/ad05@2x.png'
import ad06 from '@/assets/img/convertAdRebate/ad06@2x.png'

export default {
  data () {
    return {
      icons: [ad01, ad02, ad03, ad04, ad05, ad06]
    }
  },
  components: { defaultHead, countItem, convertList }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.convert-ad-rebate {
  padding-top: .88rem;
  .middle {
    font-size: .36rem;
  }
  .center-slot {
    font-size: .28rem;
    margin-top: .66rem;
    span {
      color: @redColor;
    }
  }
}

</style>



